import { e } from "./util";

let stack = -90;

export function showNotification(title: string, description: string) {
  const node = e("_notifiky.clone").cloneNode(true) as HTMLElement;
  const children = node.children;
  children[0].textContent = title;
  // console.log(children);
  let time_remaining = 3.0;
  stack += 100;
  const self = setInterval(() => {
    children[1].textContent = `${time_remaining.toPrecision(2)}s`;
    //@ts-ignore
    children[3].style.width = `${(time_remaining / 3) * 100 * 2.805}%`;
    time_remaining -= 0.1;
    if (time_remaining <= 0) {
      clearInterval(self);
      node.remove();
    }
    stack -= 100;
  }, 100);
  children[2].textContent = description;
  node.classList.remove("hidden");
  document.body.appendChild(node);
  node.style.bottom = `${stack}px`;
}
